<template>
    <div class="picture">
        <div :style="{backgroundImage: images[curImgIndex].url}" class="picture__main">
            <h2 style="color: #000;">{{images[curImgIndex].desc}}</h2>
        </div>
        <div class="picture__bottom" :class="{'picture__bottom-product': curNav === 'product'}">
            <div class="picture__bottom-left">
                <h3 class="picture__bottom-h3"
                    :class="{'picture__bottom-h3-chosen': curNav === 'overview'}"
                    @click="curNav ='overview'">项目背景</h3>
                <h3 class="picture__bottom-h3"
                    :class="{'picture__bottom-h3-chosen': curNav === 'product'}"
                    @click="curNav ='product'">产品设计</h3>
            </div>
            <div class="picture__bottom-right">
                <div v-for="(item, index) in images"
                     :key="item.url"
                     @click="curImgIndex = index"
                     :style="{backgroundImage: item.url}"
                     class="picture__img-item"
                     :class="{'picture__img-item-chosen': index === curImgIndex}">
                </div>
                <br style="clear:both;"/>
                <h4>描述描述描述描述描述描述描述描述描述描述描述描述描述</h4>
            </div>
        </div>
    </div>
</template>

<script>
    const url = (name) => `url(./static/img/pictureWall/${name}.jpg)`;
    export default {
        data() {
            return {
                images: [1, 2].map(name => ({
                    url: url(name),
                    name: name,
                    desc: '标题' + name
                })),
                curImgIndex: 0,
                curNav: 'overview'
            };
        },
    }
</script>

<style lang="less" scoped>
    .picture {
        margin: 0 auto;
        width: 1439px;
        text-align: left;

        .picture__main {
            width: 100%;
            height: 539px;
            background-repeat: no-repeat;
            background-size: cover;
        }

        .picture__bottom {
            display: flex;
            padding: 34px;
            background: #000;
            color: #fff;

            .picture__bottom-h3 {
                margin: 0;
                padding: 4px 8px;
                cursor: pointer;
                &:hover {
                    background: #fff;
                    color: #000;
                }
            }

            .picture__bottom-h3-chosen {
                background: #fff;
                color: #000;
            }

            .picture__bottom-left {
                margin-right: 100px;
            }

            .picture__bottom-right {

                .picture__img-item {
                    float: left;
                    margin-right: 8px;
                    width: 50px;
                    height: 50px;
                    border-radius: 2px;
                    border: 3px solid transparent;
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-position: 50% 50%;
                    cursor: pointer;

                    &:hover {
                        border-color: #989898;
                    }
                }

                .picture__img-item-chosen {
                    border-color: #989898;
                }
            }
        }

        .picture__bottom-product {
            background: linear-gradient(to right, #50bc92 0, #0ca5c7 100%);
        }

    }
</style>



